import React from "react";
import { Field, reduxForm } from "redux-form";
import { textInput } from "../ReduxForm/form";
import { enrollmentRegex } from "../ReduxForm/Regex";

const SearchDocForm = ({ handleSubmit, submitting }) => {
  return (
    <form onSubmit={handleSubmit}>
      <div className="row">
        <div className="col-lg-6">
          <div className="form-group">
            <Field
              type="date"
              name="date"
              label="Date:"
              component={textInput}
            />
          </div>
        </div>
        <div className="col-lg-6">
          <div className="form-group">
            <Field
              type="text"
              name="enrollment"
              label="Enrollment:"
              placeholder="Enrollment"
              component={textInput}
            />
          </div>
        </div>
      </div>
      <div className="row mt-2">
        <div className="col-lg-4 form-group mx-auto">
          <button
            id="bth-search"
            className="btn btn-primary btn-lg btn-block"
            disabled={submitting}
          >
            Search
          </button>
        </div>
      </div>
    </form>
  );
};

const validate = (values) => {
  console.log(values);
  const errors = {};
  if (values.date == null && values.enrollment == null) {
    return { date: "date is required" };
  }
  if (values.enrollment != null && !values.enrollment.match(enrollmentRegex)) {
    errors["enrollment"] = `Please enter valid Enrollment No.`;
  }
  return errors;
};

export default reduxForm({ form: "searchDocForm", validate })(SearchDocForm);
